.info {
  padding: 180px 0 80px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: calc(100% - 690px);
    color: #ffffff;
    position: relative;
    .info__text {
      max-width: 540px;
      margin-top: 15px;
      p {
        line-height: 120%;
      }
    }
  }
  &__right {
    width: 640px;
    color: #000000;
    margin-left: 50px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__text {
    p {
      font-size: 24px;
      line-height: 180%;
      letter-spacing: 1.2px;
    }
  }
  &__group {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    padding: 15px 5px;
  }
  &__title {
    font-family: 'BebasNeue';
    font-size: 200px;
    line-height: 105%;
  }
  &__subtitle {
    font-family: 'BebasNeue';
    font-size: 60px;
    line-height: 105%;
  }
  &__intersect {
    display: none;
    width: 115px;
    height: 115px;
    background: linear-gradient(90deg, #BB26EF 8.17%, #FF008A 103.21%);
    border-radius: 50%;
    margin: 0 0 60px auto;
  }
  &__cards {
    display: none;
    grid-template-columns: 100%;
    gap: 60px;
    margin-top: 60px;
  }
  &__card {
    color: #000;
    &-title {
      font-size: 26px;
      line-height: 120%;
      text-decoration: underline;
    }
    &-text {
      font-size: 14px;
      line-height: 180%;
      margin-top: 25px;
    }
    &-img {
      position: relative;
      padding-bottom: 42%;
      margin-top: 15px;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .info {
    &__left {
      width: calc(100% - 530px);
    }
    &__right {
      width: 500px;
      margin-left: 30px;
    }
    &__title {
      font-size: 100px;
    }
    &__subtitle {
      font-size: 40px;
    }
    &__text {
      p {
        font-size: 20px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .info {
    &__left {
      width: calc(100% - 430px);
    }
    &__right {
      width: 400px;
    }
  }
}

@media screen and (max-width: 992px) {
  .info {
    padding: 100px 0;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 60px 0 0 0;
    }
    &__title {
      font-size: 52px;
    }
    &__intersect {
      display: block;
    }
    &__cards {
      display: grid;
    }
  }
}

@media screen and (max-width: 400px) {
  .info {
    &__title {
      font-size: 46px;
    }
    &__subtitle {
      font-size: 32px;
    }
    &__text {
      p {
        font-size: 16px;
      }
    }
  }
}

