.about {
  flex-grow: 1;
  color: #000000;
  padding: 240px 0 30px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__desc {
    width: calc(100% - 730px);
  }
  &__img {
    width: 700px;
    margin-left: 30px;
    img {
      width: 100%;
    }
    .about__title {
      display: none;
      color: #FFF;
    }
  }
  &__title {
    font-size: 46px;
    line-height: 120%;
    letter-spacing: 2.3px;
  }
  &__text {
    margin-top: 120px;
    p {
      font-size: 24px;
      line-height: 180%;
      letter-spacing: 1.2px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .about {
    &__desc {
      width: calc(100% - 530px);
    }
    &__img {
      width: 500px;
    }
  }
}

@media screen and (max-width: 992px) {
  .about {
    padding: 100px 0 30px;
    &__desc {
      width: calc(100% - 430px);
    }
    &__img {
      width: 400px;
    }
    &__title {
      font-size: 32px;
    }
    &__text {
      margin-top: 30px;
      p {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .about {
    &__desc {
      width: calc(100% - 270px);
    }
    &__img {
      width: 250px;
    }
  }
}

@media screen and (max-width: 576px) {
  .about {
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
      .about__title {
        display: none;
      }
    }
    &__img {
      order: -1;
      width: 100%;
      margin: 0 0 40px 0;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.30);
      }
      .about__title {
        position: absolute;
        z-index: 1;
        left: 0;
        bottom: 100px;
        padding: 15px 5px;
        display: block;
      }
    }
  }
}