.main {
  flex-grow: 1;
  color: #ffffff;
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 180px 0;
  &__title {
    font-family: 'BebasNeue';
    font-size: 500px;
    line-height: 80%;
    font-weight: 700;
    letter-spacing: -10px;
  }
  &__subtitle {
    font-size: 40px;
    line-height: 120%;
    letter-spacing: 2px;
    margin-top: 30px;
  }
  &__btns {
    min-height: 50px;
    display: none;
    align-items: center;
    border-radius: 14px;
    background: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
    padding: 3px 14px;
    margin-top: 80px;
  }
  &__btn {
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 3.2px;
    color: #ffffff;
    background: transparent;
    padding: 0;
    position: relative;
    &:not(:first-child) {
      margin-left: 15px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -9px;
        transform: translate(0, -50%);
        width: 1px;
        height: 100%;
        background: #ffffff;
        pointer-events: none;
      }
    }
  }
  &__socials {
    display: none;
    align-items: center;
    margin-top: 40px;
  }
  &__social {
    width: 32px;
    &:not(:first-child) {
      margin-left: 60px;
    }
    img {
      width: 100%;
    }
    img:nth-child(1) {
      display: none;
    }
  }
  &__rights {
    display: none;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.7px;
    text-transform: lowercase;
    margin-top: 60px;
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__title {
      font-size: 300px;
    }
    &__subtitle {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 100px 0;
    &__title {
      font-size: 200px;
    }
    &__subtitle {
      font-size: 20px;
      margin-top: 10px;
    }
    &__btns {
      display: inline-flex;
    }
    &__socials {
      display: flex;
    }
    &__rights {
      display: block;
    }
  }
}