.rates {
  flex-grow: 1;
  color: #000;
  padding: 330px 0;
  position: relative;
  &__title {
    position: absolute;
    right: -160px;
    bottom: -80px;
    font-family: 'BebasNeue';
    font-size: 750px;
    line-height: 80%;
    font-weight: 700;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    letter-spacing: -15px;
  }
  &__items {
    max-width: 1200px;
    display: grid;
    grid-template-columns: 100%;
    gap: 130px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    border-bottom: 1px solid #000000;
    padding-bottom: 5px;
    &-title {
      font-size: 46px;
      line-height: 120%;
      letter-spacing: 2.3px;
    }
    &-text {
      font-size: 24px;
      line-height: 130%;
      letter-spacing: 1.2px;
      margin-top: 40px;
    }
    &-price {
      flex-shrink: 0;
      margin-left: 15px;
    }
    &-price span {
      font-size: 46px;
      line-height: 120%;
      letter-spacing: 2.3px;
    }
  }
}

@media screen and (max-width: 1800px) {
  .rates {
    &__title {
      right: -120px;
      font-size: 500px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .rates {
    padding: 180px 0;
    &__title {
      position: static;
      writing-mode: unset;
      transform: rotate(0);
      padding: 0 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .rates {
    &__title {
      font-size: 300px;
    }
  }
}

@media screen and (max-width: 992px) {
  .rates {
    padding: 140px 0;
    &__title {
      display: none;
    }
    &__items {
      gap: 100px;
    }
    &__item {
      &-text {
        font-size: 20px;
      }
      &-price span {
        font-size: 30px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .rates {
    &__item {
      display: block;
      padding-bottom: 0;
      &-price {
        text-align: end;
        margin: 10px 0 -5px 0;
      }
      &-price span {
        background: #ffffff;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .rates {
    &__items {
      gap: 60px;
    }
    &__item {
      &-title {
        font-size: 24px;
      }
      &-text {
        font-size: 16px;
      }
      &-price span {
        font-size: 24px;
      }
    }
  }
}