.wrap {
  height: 100%;
  position: relative;
  overflow-x: hidden;
}
.content {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.nav {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      font-size: 24px;
      line-height: 120%;
      color: #000000;
    }
    li a:hover {
      background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}