.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  color: #000000;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    flex-grow: 1;
  }
  &__right {
    position: relative;
    z-index: 3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 60px;
    padding: 15px 0;
  }
  &__close {
    display: none;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: 1.1px;
    padding-left: 22px;
    cursor: pointer;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 15px;
      height: 15px;
      background: #000000;
      border-radius: 50%;
    }
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    li {
      &:not(:first-child) {
        margin-left: 30px;
      }
    }
  }
  &__item {
    position: relative;
    &-show {
      position: relative;
      z-index: 4;
      font-size: 16px;
      line-height: 120%;
      font-weight: 900;
      letter-spacing: 0.8px;
      padding: 15px 0;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        left: -15px;
        bottom: 0;
        width: 0;
        height: 1px;
        background: #000000;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 3;
      top: 100%;
      left: 0;
      min-width: 300px;
      padding-top: 10px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      padding-bottom: 90px;
      &:after {
        content: '';
        position: absolute;
        top: -80px;
        left: 50%;
        transform: translate(-50%, - 0);
        width: 10000px;
        height: 0;
        background: #ffffff;
        padding-bottom: 90px;
        transition: all 0.3s;
      }
    }
    &-wrap {
      position: relative;
      z-index: 1;
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
    }
    &-link {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 900;
        letter-spacing: 0.8px;
        color: #000000;
        &:hover {
          background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
      i {
        display: none;
        flex-shrink: 0;
        width: 10px;
        margin-left: 10px;
      }
    }
  }
  &__item--active {
    .header__item-show {
      &:after {
        opacity: 1;
        pointer-events: all;
        width: calc(100% + 30px);
      }
    }
    .header__item-hidden {
      opacity: 1;
      pointer-events: all;
      &:after {
        height: calc(100% + 80px);
      }
    }
  }
  &__link {
    font-size: 16px;
    line-height: 120%;
    font-weight: 900;
    letter-spacing: 0.8px;
    padding: 15px 0;
    position: relative;
    z-index: 4;
    a:after {
    content: '';
    position: absolute;
    left: -15px;
    bottom: 0;
    width: 0;
    height: 1px;
    background: #000000;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
  }
    a:hover {
      &:after {
        opacity: 1;
        pointer-events: all;
        width: calc(100% + 30px);
      }
    }
  }
  &__link--active {
    a:after {
      opacity: 1;
      pointer-events: all;
      width: calc(100% + 30px);
    }
  }
  &__btns {
    min-height: 50px;
    display: flex;
    align-items: center;
    border-radius: 14px;
    background: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
    padding: 3px 14px;
  }
  &__btn {
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 3.2px;
    color: #ffffff;
    background: transparent;
    padding: 0;
    position: relative;
    &:not(:first-child) {
      margin-left: 15px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -9px;
        transform: translate(0, -50%);
        width: 1px;
        height: 100%;
        background: #ffffff;
        pointer-events: none;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 60px;
  }
  &__social {
    width: 32px;
    &:not(:first-child) {
      margin-left: 60px;
    }
    img {
      width: 100%;
    }
    img:nth-child(1) {
      display: none;
    }
  }
  &__rights {
    display: none;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.7px;
    text-transform: lowercase;
    margin-top: 30px;
  }
  &__mob-link {
    display: none;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.7px;
    &:hover {
      background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__burger {
    width: 35px;
    display: none;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    span {
      width: 15px;
      height: 15px;
      background: #000000;
      border-radius: 50%;
      &:not(:first-child) {
        margin-left: 5px;
      }
    }
  }
}
.header2 {
  color: #ffffff;
  .header__item {
    &-hidden {
      &:after {
        background: #000000;
      }
    }
    &-link span {
      color: #ffffff;
    }
  }
  .header__link {
    a:after {
      background: #ffffff;
    }
  }
  .header__social {
    img:nth-child(1) {
      display: block;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .header__burger {
    span {
      background: rgba(255, 255, 255, 0.90);
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__right {
      margin-left: 30px;
    }
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 15px;
        }
      }
    }
    &__socials {
      margin-left: 30px;
    }
    &__social {
      width: 22px;
      &:not(:first-child) {
        margin-left: 30px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__link {
      font-size: 14px;
    }
    &__item {
      &-show {
        font-size: 14px;
      }
      &-link {
        font-size: 14px;
      }
    }
    &__btns {
      min-height: 40px;
      padding: 3px 8px;
    }
    &__btn {
      font-size: 14px;
    }
    &__socials {
      margin-left: 15px;
    }
    &__social {
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    color: #000000;
    padding: 45px 0 15px;
    &__wrap {
      position: fixed;
      z-index: 4;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      align-items: start;
      background: #FFF;
      padding: 45px 30px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #000000;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
        border-radius: 3px;
      }
    }
    &__left {
      width: 100%;
      flex-grow: 0;
    }
    &__right {
      flex-direction: column;
      align-items: start;
      padding: 0;
      margin: 100px 0 0 0;
    }
    &__close {
      display: inline-flex;
    }
    &__list {
      width: 100%;
      flex-direction: column;
      align-items: start;
      margin-top: 70px;
      li {
        width: 100%;
      }
      li:not(:first-child) {
        margin: 40px 0 0 0;
      }
    }
    &__link {
      font-size: 46px;
      font-weight: 400;
      padding: 0;
      &:after {
        display: none;
      }
      &:hover {
        background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
    &__link--active {
      background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      a:after {
        display: none;
      }
    }
    &__item {
      &-show {
        font-size: 46px;
        font-weight: 400;
        padding: 0;
        &:after {
          display: none;
        }
        &:hover {
          background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
      &-hidden {
        display: none;
        opacity: 1;
        position: static;
        min-width: 100%;
        padding-top: 0;
        padding-bottom: 0;
        margin-top: 40px;
        &:after {
          display: none;
        }
      }
      &-wrap {
        gap: 30px;
        background: transparent;
        box-shadow: unset;
      }
      &-link {
        border-bottom: 1px solid #000000;
        padding-bottom: 5px;
        span {
          font-size: 24px;
          font-weight: 400;
        }
        i {
          display: block;
        }
        &:hover {
          span {
            background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
          i svg line {
            stroke: #FF008A;
          }
        }
      }
    }
    &__item--active {
      .header__item-show {
        background-image: linear-gradient(90deg, #BB26EF 2.61%, #FF008A 75.72%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      .header__item-hidden {
        display: block;
      }
    }
    &__socials {
      margin: 30px 0 0 0;
    }
    &__social {
      width: 32px;
      &:not(:first-child) {
        margin-left: 30px;
      }
    }
    &__btns {
      min-height: 50px;
      padding: 3px 14px;
    }
    &__btn {
      font-size: 16px;
    }
    &__rights {
      display: block;
    }
    &__mob-link {
      display: block;
    }
    &__burger {
      display: flex;
      margin: 0 0 0 auto;
    }
  }
  .header2 {
    .header__item {
      &-link span {
        color: #000000;
      }
    }
    .header__social {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .header {
    &__wrap {
      padding: 45px 15px;
    }
    &__right {
      margin-top: 48px;
    }
    &__list {
      margin-top: 48px;
      li {
        &:not(:first-child) {
          margin: 24px 0 0 0;
        }
      }
    }
    &__link {
      font-size: 24px;
    }
    &__item {
      &-show {
        font-size: 24px;
      }
      &-hidden {
        margin-top: 24px;
      }
      &-link {
        span {
          font-size: 16px;
        }
        i {
          width: 8px;
        }
      }
    }
  }
}